import * as THREE from "three"

import bubbleVertexShader from "../../shaders/bubble/vertex.glsl"
import bubbleFragmentShader from "../../shaders/bubble/fragment.glsl"

// import {
  // bubblesFolder,
  // camera,
  // debugObject,
  // environment,
  // environmentFolder,
  // renderer,
  // scene,
// } from "./Experience"

export class Bubbles {
  constructor(parentSketch) {
    this.parent = parentSketch
    this.bubbles = []
    this.setBubbles()
    // this.setBubblesTweaks()
  }

  setBubbles() {
    //Option1
    // this.bubbleMaterial = new THREE.MeshPhysicalMaterial({
    //   color: this.parent.debugObject.bubblesColor,
    //   envMap: this.parent.environment.environment1,
    //   roughness: 0,
    //   // transmission: 0.8,
    //   transparent: true,
    //   emissive: 0xf7931e,
    //   emissiveIntensity: 0.5,
    //   // opacity: 0.8,
    // })

    //Option2
    this.bubbleMaterial = new THREE.ShaderMaterial({
      vertexShader: bubbleVertexShader,
      fragmentShader: bubbleFragmentShader,

      uniforms: {
        uOuterColor: { value: new THREE.Color(this.parent.data.bubblesOuterColor) },
        uInnerColor: { value: new THREE.Color(this.parent.data.bubblesInnerColor) },
        uRingSize: { value: 4 },
      },
    })

    for (let i = 0; i < this.parent.data.bubblesCount; i++) {
      this.bubbleGeometry = new THREE.SphereGeometry(2.5, 50, 50)
      this.bubble = new THREE.Mesh(this.bubbleGeometry, this.bubbleMaterial)
      this.bubble.position.set(
        // Math.random() * 4 - 2,
        // Math.random(),
        // Math.random() * 4 - 2
        0,
        6,
        -30
      )
      this.bubble.rotation.set(0, -Math.PI / 2, 0)
      this.bubbles.push(this.bubble)
      this.parent.scene.add(this.bubbles[i])
    }      
  }
}
