import * as THREE from "three"

export class Lights {
  constructor(parentSketch) {
    this.parent = parentSketch
    this.setLights()
  }

  setLights() {
    const al = new THREE.AmbientLight(0xffffff, 0.5)
    this.parent.scene.add(al)

    const pl = new THREE.PointLight(0xffffff, 1.5)
    pl.position.set(0, 5, -22)
    pl.castShadow = true
    this.parent.scene.add(pl)
  }
}
