import * as THREE from "three"
import Stats from "stats.js"

import { Camera } from "./Camera"
import { Renderer } from "./Renderer"
import { Sizes } from "./Sizes"
import { Bubbles } from "./Bubbles"
import { Environment } from "./Environment"
// import { Pane } from "tweakpane"
import { WaterSurface } from "./WaterSurface"
import { Lights } from "./Lights"
import { Background } from "./Background"
import { Loaders } from "./Loaders"


export class Sketch {
  constructor(targetCanvas) {
    this.targetCanvas = targetCanvas
    this.data

    // How to use:
    //   Generate data in model
    //   Push model data to this.data
    //   Call sketch.setup() in App
  }

  setup() {
    this.loaders = new Loaders()
    this.canvas = this.targetCanvas
    this.scene = new THREE.Scene()
    this.sizes = new Sizes(this)
    this.camera = new Camera(this)
    this.renderer = new Renderer(this)
    this.scene.background = new THREE.Color(this.data.backgroundColor)
    this.lights = new Lights(this)
    this.environment = new Environment(this)
    this.background = new Background(this)
    this.bubbles = new Bubbles(this)
    this.waterSurface = new WaterSurface(this)

    this.clock = new THREE.Clock()
    this.time = Date.now()
  }

  draw() {
    const elapsedTime = this.clock.getElapsedTime()
    const currentTime = Date.now()
    const deltaTime = currentTime - this.time
    this.time = currentTime

    // Update controls
    this.camera.controls.update()

    //Update Mesh Reflector Material
    // waterSurface.water.material.update()
    // waterSurface.water.material.uniforms["time"].value += 1.0 / 240.0
    this.waterSurface.water.material.uniforms["time"].value += deltaTime * 0.00005
    // Render
    this.renderer.renderer.render(this.scene, this.camera.camera)
  }

  drawFrame() {
    window.requestAnimationFrame(this.draw.bind(this))
  }

  tick() {
    this.tickBefore()
    this.draw()
    window.requestAnimationFrame(this.tick.bind(this))
    // setTimeout(() => {
    // window.requestAnimationFrame(tick)
    // }, 1000 / 60)
    this.tickAfter()
  }

  start() {
    this.tick()
  }

  // Temporary hooks for tick(). Override these.
  tickBefore() { }
  tickAfter() { }
}

