import * as THREE from "three"
// import { canvas, sizes } from "./Experience"

export class Renderer {
  constructor(parentSketch) {
    this.parent = parentSketch
    this.renderer

    this.setRenderer()
  }

  setRenderer() {
    this.renderer = new THREE.WebGLRenderer({
      canvas: this.parent.canvas,
      antialias: true,
    })
    this.renderer.setSize(this.parent.sizes.width, this.parent.sizes.height)
    this.renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
    // this.renderer.setClearColor(0x1395ba)
    this.renderer.shadowMap.enabled = true
  }
}
