import * as THREE from "three"

export class Model {
    constructor(parent, random) {
        this.parent = parent
        this.random = random
        this.setData()
        this.setBindings()
    }

    // Populate default data
    setData() {
        // Default data
        this.data = {
            backgroundColor: "#e5001e",
            backgroundLightColor: "#ffffff",
            backgroundLightStrength: 3.8,
            backgroundLightHeight: 2.17,
            backgroundLightVerticalPosition: 2.10,

            bubblesColor: "#00c3ff",
            bubblesOuterColor: "#a0e8fe",
            bubblesInnerColor: "#ffffff",
            bubblesRingSize: 4,
            bubblesY: 6,
            bubblesCount: 1,
            bubblesScale: 1,
        }
    }

    // Define bindings
    setBindings() {
        this.bindings = {
            backgroundColor: () => {
                this.parent.background.background.material.uniforms.uBackgroundColor.value = new THREE.Color(this.data.backgroundColor)
            },
            backgroundLightColor: () => {
                this.parent.background.background.material.uniforms.uLightColor.value = new THREE.Color(this.data.backgroundLightColor)
            },
            backgroundLightStrength: () => {
                this.parent.background.background.material.uniforms.uBackgroundLightStrength.value = this.data.backgroundLightStrength
            },
            backgroundLightHeight: () => {
                this.parent.background.background.material.uniforms.uBackgroundLightHeight.value = this.data.backgroundLightHeight
            },
            backgroundLightVerticalPosition: () => {
                this.parent.background.background.material.uniforms.uBackgroundLightVerticalPosition.value = this.data.backgroundLightVerticalPosition
            },
            bubblesOuterColor: () => {
                this.parent.bubbles.bubbleMaterial.uniforms.uOuterColor.value = new THREE.Color(this.data.bubblesOuterColor)
            },
            bubblesInnerColor: () => {
                this.parent.bubbles.bubbleMaterial.uniforms.uInnerColor.value = new THREE.Color(this.data.bubblesInnerColor)
            },
            bubblesRingSize: () => {
                this.parent.bubbles.bubbleMaterial.uniforms.uRingSize.value = this.data.bubblesRingSize
            },
            bubblesY: () => {
                this.parent.bubbles.bubble.position.y = this.data.bubblesY
            },
        }
    }

    // Update a parameter's value and optionally perform related action
    update(parameter, value, runAction = true) {
        this.data[parameter] = value
        if (runAction && parameter in this.bindings) {
            this.bindings[parameter]()
        }
    }

    // Refresh view with all actions
    refresh() {
        for (const parameter in this.bindings) {
            this.bindings[parameter]()
        }
    }
}