import * as THREE from "three"
// import { backgroundFolder, debugObject, scene } from "./Experience"

import backgroundVertexShader from "../../shaders/background/vertex.glsl"
import backgroundFragmentShader from "../../shaders/background/fragment.glsl"

export class Background {
  constructor(parentSketch) {
    this.parent = parentSketch
    this.setBackground()
    // this.setBackgroundTweaks()
  }

  setBackground() {
    this.background = new THREE.Mesh(
      new THREE.PlaneGeometry(150, 150),
      new THREE.ShaderMaterial({
        vertexShader: backgroundVertexShader,
        fragmentShader: backgroundFragmentShader,

        uniforms: {
          uBackgroundColor: {
            value: new THREE.Color(this.parent.data.backgroundColor),
          },
          uLightColor: {
            value: new THREE.Color(this.parent.data.backgroundLightColor),
          },
          uBackgroundLightStrength: { value: 3.8 },
          uBackgroundLightHeight: { value: 2.17 },
          uBackgroundLightVerticalPosition: { value: 2.1 },
        },
      })
    )
    this.background.position.set(0, 0, -40)
    this.parent.scene.add(this.background)
  }
}
