import { Pane } from 'tweakpane';
import * as EssentialsPlugin from '@tweakpane/plugin-essentials';

export class Controller {
    constructor(sketch, model, presets) {
        this.sketch = sketch
        this.model = model
        this.presets = presets
        this.pane

        this.presetParams = {
            backgroundLightStrength: { min: 0, max: 10 },
            backgroundLightHeight: { min: 0, max: 4 },
            backgroundLightVerticalPosition: { min: 0, max: 5 },
            bubblesRingSize: { min: 0, max: 20 },
            bubblesY: { min: -5, max: 15 },
        };

        this.init()
    }

    init() {
        this.pane = new Pane()
        this.pane.registerPlugin(EssentialsPlugin);

        this.addSketchProperties()
        this.addBackgroundControls()
        this.addBubbleControls()

        this.addPresets()
        // this.addGenArray()
        // const preset = this.pane.exportPreset();
        // console.log(preset)
    }

    addSketchProperties() {
        // Sketch Properties
        const sketchPropertiesFolder = this.pane.addFolder({
            title: 'Sketch Properties'
        })

        const sketchProperites = {
            hash: this.model.random.tokenData.hash,
        };

        sketchPropertiesFolder.addInput(sketchProperites, 'hash',
            {
                presetKey: 'sketch_hash',
            }
        );

        const btnRedraw = this.pane.addButton({
            title: 'Redraw',
        });
        btnRedraw.on('click', () => {
            this.randomizeWithPreset()
            this.model.refresh()
            this.updateFromModel()
            this.pane.importPreset({
                sketch_hash: this.model.random.tokenData.hash
            })
        });

        const btnRandomize = this.pane.addButton({
            title: 'Randomize',
        });
        btnRandomize.on('click', () => {
            console.log(this.model)
            console.log(this.model.random)
            this.model.random.generateNewToken()
            this.presets.updateModel()
            this.randomizeWithPreset()
            this.model.refresh()
            this.updateFromModel()
            this.pane.importPreset({
                sketch_hash: this.model.random.tokenData.hash
            })
        });
    }

    addBackgroundControls() {
        const folder = this.pane.addFolder({ title: "Background" })

        folder
            .addInput(this.model.data, 'backgroundColor', {
                label: 'Background Color',
            })
            .on("change", (ev) => {
                this.model.update('backgroundColor', ev.value)
            })

        folder
            .addInput(this.model.data, "backgroundLightColor", {
                label: "Background Light",
            })
            .on("change", (ev) => {
                this.model.update('backgroundLightColor', ev.value)
            })

        folder
            .addInput(this.model.data, 'backgroundLightStrength', {
                min: 0,
                max: 10,
                step: 0.01,
                label: "Light Strength",
            })
            .on('change', (ev) => {
                this.model.update('backgroundLightStrength', ev.value)
            })

        folder
            .addInput(this.model.data, 'backgroundLightHeight', {
                min: 0,
                max: 4,
                step: 0.01,
                label: "Light Height",
            })
            .on('change', (ev) => {
                this.model.update('backgroundLightHeight', ev.value)
            })

        folder
            .addInput(this.model.data, 'backgroundLightVerticalPosition', {
                min: 0,
                max: 5,
                step: 0.01,
                label: "Light Vertical Position",
            })
            .on('change', (ev) => {
                this.model.update('backgroundLightVerticalPosition', ev.value)
            })
    }

    addBubbleControls() {
        const folder = this.pane.addFolder({ title: "Bubbles" })

        folder
            .addInput(this.model.data, 'bubblesOuterColor', {
                label: 'Bubble Outside Color',
            })
            .on("change", (ev) => {
                this.model.update('bubblesOuterColor', ev.value)
            })

        folder
            .addInput(this.model.data, 'bubblesInnerColor', {
                label: 'Bubble Inside Color',
            })
            .on("change", (ev) => {
                this.model.update('bubblesInnerColor', ev.value)
            })

        folder
            .addInput(this.model.data, 'bubblesRingSize', {
                min: 0,
                max: 20,
                step: 0.01,
                label: "Ring Size",
            })
            .on("change", (ev) => {
                this.model.update('bubblesRingSize', ev.value)
            })

        folder
            .addInput(this.model.data, 'bubblesY', {
                min: -5,
                max: 15,
                step: 0.01,
                label: "Bubble Height",
            })
            .on("change", (ev) => {
                this.model.update('bubblesY', ev.value)
            })
    }

    randomizeWithPreset() {
        // This takes a snap shot of the current Pane's state,
        // then uses the "preset_" values to create virtual adapters that
        // supply the ranges for randomization.

        const data = this.pane.exportPreset()

        // Original by-hand method:
        // const guiPresetController = {
        //     backgroundLightStrength: {
        //         func: this.model.random.random_num.bind(this.model.random),
        //         args: [data['preset_backgroundLightStrength'].min, data['preset_backgroundLightStrength'].max]
        //     },
        // }

        const guiPresetController = {}
        const controllerNames = Object.keys(this.presetParams)

        for (const cn of controllerNames) {
            const key = 'preset_' + cn

            guiPresetController[cn] = {
                func: this.model.random.random_num.bind(this.model.random),
                args: [data[key].min, data[key].max]
            }
        }

        this.presets.presets['guiPresetController'] = guiPresetController
        this.presets.select('guiPresetController')
    }

    addPresets() {
        // Presets
        const folder = this.pane.addFolder({
            title: 'Presets'
        })

        folder
            .addInput(this.presetParams, 'backgroundLightStrength', {
                min: 0,
                max: 10,
                step: 0.01,
                label: "Light Strength",
                presetKey: 'preset_backgroundLightStrength',
            })

        folder
            .addInput(this.presetParams, 'backgroundLightHeight', {
                min: 0,
                max: 4,
                step: 0.01,
                label: "Light Height",
                presetKey: 'preset_backgroundLightHeight',
            })

        folder
            .addInput(this.presetParams, 'backgroundLightVerticalPosition', {
                min: 0,
                max: 5,
                step: 0.01,
                label: "Light Vertical Position",
                presetKey: 'preset_backgroundLightVerticalPosition',
            })

        folder
            .addInput(this.presetParams, 'bubblesRingSize', {
                min: 0,
                max: 20,
                step: 0.01,
                label: "Ring Size",
                presetKey: 'preset_bubblesRingSize',
            })

        folder
            .addInput(this.presetParams, 'bubblesY', {
                min: -5,
                max: 15,
                step: 0.01,
                label: "Bubble Height",
                presetKey: 'preset_bubblesY',
            })

        console.log(this.pane)
    }

    updateFromModel() {
        this.pane.importPreset(this.model.data)
    }
}